*,*::before,*::after{
    box-sizing: border-box;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}
body{
    font-family: 'Roboto','Noto Sans TC', sans-serif;
    font-size: 16px;
    line-height: 24px;
}

.header, .footer , a{
    background: #aa0601;
    color: #fff;
}
a{
    text-decoration: none;
}

h1{
    background: url(../images/logo-white.png);
    width: 80px;
    height: 48.57px;
    background-position: center;
    background-size: cover;
    display: block;
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
    margin: 16px 0 15.43px 0;
}
.header ul li a{
    font-size: 20px;
    line-height: 30px;
    display: block;
    padding: 25px 0;
    margin: 0 0 0 64px;
}
.header .container, .top-menu ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.hambuger{
    display: none;
    color: #fff;
    font-size: 40px;
    margin: 0 15px 0 0;
}

.bottom-menu, .bottom-menu-list, .copyright, .copyright ul{
    display: flex;
    justify-content: space-between;
}
.bottom-menu{
    padding: 48px 0 32px 0;
    border-bottom: 1px solid #fff;
}
.bottom-menu-list{
    margin: 0 0 36px 0;
    align-items: center;
}
.bottom-menu-list li a{
    display: block;
    margin: 0 48px 0 0;
}
.sns{
    display: flex;
}
.sns li a{
    display: block;
    width: 48px;
    height: auto;
    margin: 0 0 0 32px;
}
.contactInfo li{
    font-size: 36px;
    line-height: 54px;
    margin: 8px 0 0 0;
}
.contactInfo span{
    font-size: 36px;
}
.copyright li a{
    font-size: 14px;
    line-height: 21px;
    display: block;
    margin: 0 0 0 48px;
}
.copyright{
    padding: 32px 0;
}

@media (max-width:992px) {
    .bottom-menu-list{
        margin: 0 0 32px 0;
    }
    .sns li a{
        width: 40px;
        margin: 0 0 0 16px;
    }
    .copyright{
        padding: 32px 0 40px 0;
    }  
}

@media (max-width:767px) {
    .header .container{
        position: relative;
    }
    .header ul{
        background: #AA0601;
        position: absolute;
        top: 80px;
        right: 0;
        flex-direction: column;
        text-align: center;
    }
    .header ul li a{
        font-size: 16px;
        line-height: 24px;
        margin: 0;
        width: 174px;
        border: 1px solid #fff;
    }
    .hambuger{
        display: block;
    }
    .top-menu ul{
        display: none;
    }

    .bottom-menu{
        padding: 24px 0;
    }
    .bottom-menu-list{
        display: none;
    }
    .sns li a{
        width: 24px;
        margin: 0 0 0 8px;
    }
    .contactInfo li, .copyright li a{
        font-size: 16px;
        line-height: 24px;
    }
    .contactInfo span{
        font-size: 16px;
    }
    .copyright, .copyright ul{
        display: block;
    }
    .copyright{
        padding: 16px 0 24px 0;
    }
    .copyright li a{
        margin: 8px 0 0 0;
    }
}